/* Стили компонента Gallery */

.gallery {
    display: grid;
    max-width: 1440px;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-left: auto;
    margin-right: auto;
    list-style: none;
}


/* Стили кнопки Load more */

.button {
    padding: 8px 16px;
    border-radius: 3px;
    background-color: #3884ff;
    transition: all 200ms ease;
    text-align: center;
    display: block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 16px;
    line-height: 24px;
    font-style: normal;
    min-width: 180px;
    margin: 0 auto;
}

.button:hover,
.button:focus {
    background-color: #1f65d6;
}