.basicLightbox {
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .8);
    z-index: 1000;
}

.modalContent {
    background-color: #fff;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 800px;
    height: 600px;
}